<template>
    <div>
        <p class="title">{{ title }}</p>
        <div class="divider"></div>
        <div v-loading="loading">
            <div class="content" v-html="content">
                
            </div>

            <mavon-editor
                class="markdown-edit"
                v-model="markdown" 
                @change="updateDoc" 
                :toolbars="toolbars"
                :subfield="false"/>

        </div>
    </div>
</template>

<script>
const toolbars = {
    bold: true, // ç²ä½
    italic: true, // æä½
    header: true, // æ é¢
    underline: true, // ä¸åçº¿
    strikethrough: true, // ä¸­åçº¿
    mark: true, // æ è®°
    superscript: true, // ä¸è§æ 
    subscript: true, // ä¸è§æ 
    quote: true, // å¼ç¨
    ol: true, // æåºåè¡¨
    ul: true, // æ åºåè¡¨
    link: true, // é¾æ¥
    imagelink: true, // å¾çé¾æ¥
    code: true, // code
    table: true, // è¡¨æ ¼
    fullscreen: false, // å¨å±ç¼è¾
    readmodel: false, // æ²æµ¸å¼éè¯»
    htmlcode: false, // å±ç¤ºhtmlæºç 
    help: false, // å¸®å©
    /* 1.3.5 */
    undo: true, // ä¸ä¸æ­¥
    redo: true, // ä¸ä¸æ­¥
    trash: true, // æ¸ç©º
    save: false, // ä¿å­ï¼è§¦åeventsä¸­çsaveäºä»¶ï¼
    /* 1.4.2 */
    navigation: false, // å¯¼èªç®å½
    /* 2.1.8 */
    alignleft: true, // å·¦å¯¹é½
    aligncenter: true, // å±ä¸­
    alignright: true, // å³å¯¹é½
    /* 2.2.1 */
    subfield: false, // ååæ æ¨¡å¼
    preview: false // é¢è§
};


export default {
    name: 'Archives',
    data() {
        return {
            title: '',
            content: '',
            markdown: '',
            toolbars,
            loading: true
        }
    },
    methods: {
        updateDoc(value, render) {
            this.content = render;
        }
    },
    created() {
        this.$emit('tooltip', {
            title: 'Back to Index',
            routeName: 'Index'
        });

        this.loading = true;
        this.$axios({
            method: 'GET',
            url: this.serverAddress + '/archives/archives?id=' + this.$route.params.id
        }).then((response) => {
            let info = response.data;
            let status = info.status;
            switch(status) {
                case 200:
                    this.title = info.data.title;
                    this.content = info.data.content;
                    this.loading = false;
                    break;
                case 404:
                    this.title = "404 ç©ºç~";
                    this.content = "<center>è¿éæ²¡æä½ æ³è¦ç</center>";
                    break;

            }   
        });
    }
}
</script>

<style scoped>
    .title {
        margin: 0;
        font-size: 20px;
    }

    .content {
        margin: 0 auto;
        max-width: 450px;
        text-align: left;
    }

    .markdown-edit {
        margin: 0 auto;
        margin-top: 30px;
        max-width: 600px;
    }
</style>


